*{
  font-family: 'Roboto',sans-serif;
  font-size: 18px;
}
body{
  display:flex;
  justify-content: center;
  align-items:center;
}
.container{
  top:50%;
  left:50%;
  position: absolute;
  transform:translate(-50%,-50%);
  margin:0 auto;
  width:256 px;
  text-align: right;
  border:10px solid black;
  border-radius: 5px;
  
}

.keypad{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(60px,auto);
}
input[type="number"]{
  height:75px;
  width:249px ;
  background-color: black;
color:black;
}
 
#clear{
  grid-column:1/3;
  grid-row:1;
  color:black;
  
}
#backspace{
  color:black;
}
button{
  margin:5px;
  border:none;
  border-radius: 5px;
  background: black;
  color: aliceblue;
  


}

.highlight{
  background-color: rgb(0, 195, 255);
  color: black;
}

#equalto{
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row: 5;
 
}

